*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  height: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
  padding: 0;
  margin: 0;
}
.page {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Coolvetica", "Arial", sans-serif;
  background-image: url("../img/shape.svg");
  background-repeat: round;
  background-size: 60px 130px;
  background-position: center;
  background-attachment: fixed;
}
@font-face {
  font-family: "Coolvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Coolvetica.woff2") format("woff2"), url("../fonts/Coolvetica.woff") format("woff");
}
@keyframes letter {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes nav-link {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  min-height: 100vh;
  background-image: url("../img/header-blop.svg");
  background-size: 1000px 800px;
  background-repeat: no-repeat;
  background-position: top left;
}
@media (max-width: 767px) {
  .header {
    background-size: 840px 680px;
    background-position: -20px -20px;
  }
}
.header__logo {
  color: #000c24;
  transition: 0.3s;
}
@media (max-width: 1135px) {
  .header__logo {
    color: #ffffff;
  }
}
.header__logo:hover {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
}
.header__logo:active {
  opacity: 0.3;
}
.letter {
  display: inline-block;
  transition: 0.15s;
}
.letter:hover {
  color: #246dff;
}
.navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
}
@media (max-width: 767px) {
  .navigation {
    padding: 20px 40px;
  }
}
.navigation__toggler {
  display: none;
  width: 40px;
  height: 36px;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .navigation__toggler {
    display: block;
  }
}
.navigation__toggler-line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
}
.navigation__toggler-line::before {
  position: absolute;
  top: 8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
}
.navigation__toggler-line::after {
  position: absolute;
  top: -8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
}
.navigation__toggler--active .navigation__toggler-line {
  transform: translate3d(-100px, 0, 0);
}
.navigation__toggler--active .navigation__toggler-line::before {
  transform: translate3d(100px, -8px, 0) rotate(45deg);
}
.navigation__toggler--active .navigation__toggler-line::after {
  transform: translate3d(100px, 8px, 0) rotate(-45deg);
}
.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  gap: 50px;
  list-style-type: none;
  transition: 0.4s;
}
@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    top: 0;
    left: -100vw;
    height: 100vh;
    width: 50vw;
    padding-top: 100px;
    padding-left: 60px;
    flex-direction: column;
    background-color: #000c24;
    z-index: 1;
  }
}
.navigation__list--active {
  top: 0;
  left: 0;
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  opacity: 0.6;
  transition: 0.3s;
}
.navigation__link:hover {
  opacity: 1;
}
.navigation__link--active {
  position: relative;
  color: #000c24;
  z-index: 1;
  opacity: 1;
}
.navigation__link--active::before {
  position: absolute;
  top: -150%;
  transition: 0.5s;
  left: -30%;
  display: block;
  width: 200%;
  height: 98px;
  background-image: url("../img/link-active.svg");
  background-size: 100% 98px;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  z-index: -1;
  animation-name: nav-link;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
@media (max-width: 767px) {
  .navigation__link--active::before {
    top: -70%;
    height: 65px;
    background-size: 100% 65px;
  }
}
.hero {
  padding-left: 80px;
  padding-top: 40px;
}
@media (max-width: 767px) {
  .hero {
    padding-left: 40px;
  }
}
.hero__title {
  margin: 0;
  color: #ffffff;
  font-size: 50px;
  letter-spacing: -5px;
  transform: skewX(-45deg) translateX(350px);
  transition: 0.6s;
}
@media (max-width: 767px) {
  .hero__title {
    font-size: 40px;
  }
}
.hero__description {
  margin: 40px 0;
  color: #ffffff;
  font-size: 18px;
}
.hero__social-list {
  display: flex;
  padding: 0;
  gap: 30px;
  list-style-type: none;
}
.hero__social-button {
  color: #ffffff;
  transition: 0.3s;
}
.hero__social-button:hover {
  color: #246dff;
}
.hero__social-button:active {
  opacity: 0.6;
}
.animated {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.main {
  flex-grow: 1;
}
.main__up-btn {
  position: fixed;
  bottom: 40px;
  right: 40px;
  display: none;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  z-index: 1;
  cursor: pointer;
}
.section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  min-height: 100vh;
  max-height: 100vh;
  padding: 300px 160px;
  direction: rtl;
  color: #000c24;
}
@media (max-width: 767px) {
  .section {
    grid-template-columns: 1fr;
    padding: 100px 0;
    direction: ltr;
  }
}
.section--odd {
  direction: ltr;
  color: #ffffff;
  background-color: #000c24;
  box-shadow: 0 0 30px 10px #000c24;
}
.section__text-wrapper {
  display: flex;
  padding: 0 40px;
  flex-direction: column;
  align-items: flex-start;
  transform: translateX(1000px);
  transition: 0.8s;
}
.section__title {
  font-size: 50px;
  margin: 0;
  text-transform: capitalize;
  transform: skewX(45deg) translateX(-250px);
  transition: 0.8s;
}
.section__description {
  font-size: 18px;
  letter-spacing: 0.1em;
  transform: skewX(-80deg) translateX(-450px);
  transition: 0.8s;
}
.section__buttons-list {
  display: flex;
  padding: 0;
  gap: 40px;
  list-style-type: none;
}
.section__button {
  display: block;
  color: inherit;
  transition: 0.3s;
}
.section__button:hover {
  color: #246dff;
}
.section__button:active {
  opacity: 0.6;
}
.section__image-wrapper {
  transform: translateX(-1000px) skewX(-45deg);
  transition: 0.7s;
}
@media (max-width: 767px) {
  .section__image-wrapper {
    justify-self: center;
  }
}
@media (max-width: 767px) {
  .section__image {
    width: 340px;
    height: auto;
  }
}
.section--odd .section__text-wrapper {
  transform: translateX(-1000px);
}
.section--odd .section__image-wrapper {
  transform: translateX(1000px) skewX(45deg);
}
.section--odd .section__title {
  transform: skewX(-45deg) translateX(250px);
}
.footer {
  padding: 8px 40px;
  background-color: #ffffff;
}
